import React,{ Suspense, lazy } from 'react';
import { BrowserRouter, Route, Routes, Navigate } from 'react-router-dom';
import { ConfigProvider } from 'antd';
import zhCN from 'antd/lib/locale/zh_CN';
import routes from './router';
const DataDictionarySet = lazy(() => import('../page/dataDictionarySet/index'));

const Router = () => {
     return (
            <ConfigProvider locale={zhCN}>
                <BrowserRouter basename={window.__POWERED_BY_QIANKUN__ ? '/layout/basic' : '/'}>
                    <Suspense fallback={null}>
                        <Routes>
                            <Route path="/" exact element={<DataDictionarySet />} />
                            {
                                routes.map(Item => {
                                    return (
                                        <Route path={Item.path} key={Item} element={<Item.element />} /> 
                                    )
                                })
                            }
                            <Route path="*" element={<Navigate to="/" />} />
                        </Routes>
                    </Suspense>
                </BrowserRouter>
            </ConfigProvider>
     )
}
export default Router;